.shop-header {
  border-bottom: 2px solid rgba(255, 255, 255, 0.808);
  display: flex;
  justify-content: space-between;
  background: #203127a8;
}

.shop-header a {
  color: rgb(255, 255, 255);
  text-decoration: none;
}
.shop-header a:hover {
  color: rgba(185, 185, 185, 0.411);
}

.shop-header .logo {
  font-size: 4rem;
  padding-left: 1rem;
}

.shop-header .shopping-cart {
  align-self: center;
  font-size: 1.3rem;
  padding-right: 1rem;
  text-align: center;
}

.shop-header .shopping-cart .cart-icon {
  font-size: 2.2rem;
  margin-right: 10px;
}




.preview-item {
  display: flex;
  align-items: flex-start;
  color: rgb(39, 24, 24);
}
.preview-item .item-details-img {
  width: 30vh;
  height: auto;
  margin: 2vh 2.5vh;
}

.preview-item .item-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.preview-item .item-details h4 {
  font-size: 40px;
}

.preview-item .item-details span {
  width: 50%;
}

.preview-item .item-details button {
  width: 22vh;
  margin: 1vh 0;
}


.item-list {
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background: rgba(255, 255, 255, 0.575);
  padding: 0;
}

.show-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 14vh;
  height: 12vh;
  margin: 1.5vh;
  /* border: 1px solid #fff;
  border-radius: 25px; */
}
.show-item img {
  width: 9vh;
  height: 9vh;
}

.show-item a{
  font-size: 20px;
  font-weight: 500;
  color: #000;
}
.pagination {
  cursor: pointer;
  display: flex;
  justify-content: center;
  margin: 1vh 0;
}
body {
    background: url(/Pokemon-App/static/media/bg1.26b5551b.png) no-repeat fixed center / cover;
    color: #fff;
    margin: 0;
    padding: 0;
}
